"use client";

import { useState, useCallback } from "react";
import Header from "../components/Header";
import SearchSection from "../components/SearchSection";
import WeatherContent from "../components/WeatherContent";

export default function Page() {
  const [unit, setUnit] = useState("metric");
  const [weatherData, setWeatherData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [searchResults, setSearchResults] = useState([]);
  const [selectedDay, setSelectedDay] = useState("Tuesday");

  const handleSearch = useCallback(
    async (location) => {
      setLoading(true);
      setError(null);

      try {
        await new Promise((resolve) => setTimeout(resolve, 1500));

        const mockData = {
          location: location || "Berlin, Germany",
          date: "Tuesday, Aug 5, 2025",
          temperature: unit === "metric" ? 20 : 68,
          feelsLike: unit === "metric" ? 18 : 64,
          humidity: 46,
          wind: unit === "metric" ? 14 : 9,
          windUnit: unit === "metric" ? "km/h" : "mph",
          precipitation: unit === "metric" ? 0 : 0,
          precipitationUnit: unit === "metric" ? "mm" : "in",
          icon: "‚òÄÔ∏è",
          hourly: [
            { time: "3 PM", temp: unit === "metric" ? 20 : 68, icon: "‚òÅÔ∏è" },
            { time: "4 PM", temp: unit === "metric" ? 20 : 68, icon: "‚òÅÔ∏è" },
            { time: "5 PM", temp: unit === "metric" ? 20 : 68, icon: "‚õÖ" },
            { time: "6 PM", temp: unit === "metric" ? 19 : 66, icon: "‚òÅÔ∏è" },
            { time: "7 PM", temp: unit === "metric" ? 18 : 64, icon: "‚òÅÔ∏è" },
            { time: "8 PM", temp: unit === "metric" ? 18 : 64, icon: "üåä" },
            { time: "9 PM", temp: unit === "metric" ? 17 : 63, icon: "‚òÅÔ∏è" },
            { time: "10 PM", temp: unit === "metric" ? 17 : 63, icon: "‚òÅÔ∏è" },
          ],
          daily: [
            {
              day: "Tue",
              icon: "‚òÅÔ∏è",
              high: unit === "metric" ? 20 : 68,
              low: unit === "metric" ? 14 : 57,
            },
            {
              day: "Wed",
              icon: "üåßÔ∏è",
              high: unit === "metric" ? 21 : 70,
              low: unit === "metric" ? 15 : 59,
            },
            {
              day: "Thu",
              icon: "‚òÄÔ∏è",
              high: unit === "metric" ? 24 : 75,
              low: unit === "metric" ? 14 : 57,
            },
            {
              day: "Fri",
              icon: "‚õÖ",
              high: unit === "metric" ? 25 : 77,
              low: unit === "metric" ? 13 : 55,
            },
            {
              day: "Sat",
              icon: "üåßÔ∏è",
              high: unit === "metric" ? 21 : 70,
              low: unit === "metric" ? 15 : 59,
            },
            {
              day: "Sun",
              icon: "üåä",
              high: unit === "metric" ? 25 : 77,
              low: unit === "metric" ? 16 : 61,
            },
            {
              day: "Mon",
              icon: "üåä",
              high: unit === "metric" ? 24 : 75,
              low: unit === "metric" ? 15 : 59,
            },
          ],
        };

        setWeatherData(mockData);
        setSearchResults([]);
      } catch (err) {
        setError(
          "We couldn't connect to the server (API error). Please try again in a few moments."
        );
      } finally {
        setLoading(false);
      }
    },
    [unit]
  );

  const handleSearchChange = (value) => {
    if (value.trim()) {
      setSearchResults([
        `${value}`,
        `${value}, France`,
        `${value}, Germany`,
        `${value}, USA`,
      ]);
    } else {
      setSearchResults([]);
    }
  };

  const handleSelectLocation = (location) => {
    setSearchResults([]);
    handleSearch(location);
  };

  const handleUnitChange = (newUnit) => {
    setUnit(newUnit);
  };

  return (
    <div className="min-h-screen bg-slate-950">
      <Header unit={unit} onUnitChange={handleUnitChange} />
      <main className="container mx-auto px-4 py-8 md:py-12 max-w-7xl">
        <SearchSection
          onSearch={handleSearch}
          onSearchChange={handleSearchChange}
          searchResults={searchResults}
          onSelectLocation={handleSelectLocation}
          loading={loading}
        />
        <WeatherContent
          weatherData={weatherData}
          loading={loading}
          error={error}
          onRetry={() => handleSearch(weatherData?.location)}
          selectedDay={selectedDay}
          onSelectDay={setSelectedDay}
        />
      </main>
    </div>
  );
}
